
body {
  font-family: Arial, sans-serif;
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
}

h1 {
  color: #343a40;
  text-align: center;
  margin-top: 20px;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  background: #ffffff;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

h2 {
  color: #6c757d;
  margin-bottom: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

table th,
table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #dee2e6;
}

table th {
  background-color: #343a40;
  color: white;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 3px;
  cursor: pointer;
  margin-right: 5px;
}

button:hover {
  background-color: #0056b3;
}

input[type="text"],
input[type="date"] {
  padding: 10px;
  margin: 5px 0;
  width: calc(100% - 22px);
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-radius: 5px;
}

input[type="text"]:focus,
input[type="date"]:focus {
  outline: none;
  border-color: #80bdff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.add-beneficiary {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.add-beneficiary button {
  padding: 10px 20px;
}

.status-active {
  color: green;
}

.status-inactive {
  color: red;
}